@import url("https://fonts.googleapis.com/css2?family=Inter&family=Lato&family=Libre+Baskerville&family=Merriweather:wght@700&family=Work+Sans&display=swap");
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: "Work Sans", sans-serif;
}

html {
    color: rgb(96 101 123);
}

body {
    display: flex;
    justify-content: center;
    align-items: center;
    align-self: center;
    background: rgb(255 255 255);
}

.container {
    padding: 10px;
    margin-top: 10px;
}

h2 {
    color: rgb(43 45 56);
    text-align: center;
    font-size: 48px;
    letter-spacing: -0.2px;
    font-family: "Merriweather", serif;
}

p {
    max-width: 50rem;
    text-align: center;
    margin-top: 10px;
    margin-bottom: 10px;
}

.main-content {
    max-width: 50rem;
    width: 100%;
    min-height: 400px;
    height: auto;
    padding: 18px 18px 120px 18px;
    position: relative;
    resize: none;
    box-shadow: 0 12px 48px 0px rgb(109 117 141 / 20%);
    background: rgb(255 255 255);
    border: 0.5px solid rgb(231 233 245);
    border-radius: 16px;
}

.main-content:focus {
    border: 0.3px solid rgb(43, 45, 56, 0.3);
    outline: none;
}

p,
.main-content {
    font-size: 18px;
    letter-spacing: 1px;
    word-spacing: 2px;
    line-height: 1.6;
}

.btn-style {
    display: flex;
}

button {
    background: rgb(17 166 131);
    color: rgb(255 255 255);
    border-radius: 6px;
    padding: 16px 32px;
    border: none;
    font-size: 18px;
    letter-spacing: 1px;
    margin: auto;
    display: flex;
    position: relative;
    margin-top: -80px;
    cursor: pointer;
}

button:focus,
button:hover {
    background: rgb(42, 201, 164);
    box-shadow: 0 5px 30px 0 rgba(0, 0, 0, 0.1);
}

@media screen and (max-width: 768px) {
    .container {
        margin-top: 20px;
    }
    h2 {
        font-size: 28px;
    }
    p {
        font-size: 16px;
        word-spacing: normal;
        margin-bottom: 40px;
        margin-top: 15px;
    }
    .main-content {
        font-size: 16px;
        word-spacing: normal;
    }
    button {
        padding: 12px 28px;
        font-size: 16px;
        margin-top: -70px;
    }
}